
import {defineComponent} from "vue";

export default defineComponent({
	emits:['update:modelValue'],
	props:{
		modelValue:{
			type: Boolean,
			required: true,
		}
	},
	setup(props, context){
		const closeModal = () => context.emit('update:modelValue', false);
		
		return{
			closeModal,
		}
	}
});
