
import {defineComponent} from "vue"

export default defineComponent({
    emits: ['update:modelValue'],
    props: {
        modelValue: {
            type: Boolean,
            required: true,
        }
    },
    setup(props, context) {
        const closeMenu = () => context.emit('update:modelValue', false);

        return {
            closeMenu,
        }
    }
})
